let stateNameMap = {
  "initial": "",
  "terminate": "",
  "activate": ""
};
let eventMap = {
  "compoundEvents": function (t) {
    let resultStr = [];
    t.params.forEach((event)=> {
      resultStr.push(eventMap[event.type](event));
    })
    return resultStr.join("");
  },
  "arrivalDate" : function (e) {
    if( e.params[0] == 1) {
      return ""+ e.params[1];
    }else {
      return ""+  e.params[1]+ "";
    }
  },
  "period": function (e) {
    return ""+e.params[0]+""
  },
  "signing": function (e) {
    return ""
  },
  "contractGuaranty": function (e) {
    return ''
  }
};
module.exports = {
  stateNameMap: stateNameMap,
  eventMap : eventMap
}



