<template>
  <section>
    <el-input placeholder="" v-model="query" class="query-input">
      <el-button slot="append" icon="search" type="primary" @click="queryHandler"></el-button>
    </el-input>
    <table-view class="contracts-list" :loader="loader()">
      <el-table-column
        prop="resourceDetail.resourceName"
        label="">
      </el-table-column>
      <el-table-column
        prop="contractId"
        align="center"
        label=" ID">
      </el-table-column>
      <el-table-column
        prop="resourceId"
        label=" ID"
        align="center"
        width="350px">
      </el-table-column>
      <el-table-column
        prop="resourceDetail.resourceType"
        align="center"
        label="">
      </el-table-column>
      <el-table-column
        prop="status"
        align="center"
        width="120px"
        label="">
        <template slot-scope="scope">
          <div v-if="scope.row.status==0"></div>
          <div v-if="scope.row.status==1"></div>
          <div v-if="scope.row.status==2"></div>
          <div v-if="scope.row.status==3"></div>
          <div v-if="scope.row.status==4"></div>
          <div v-if="scope.row.status==4"></div>
        </template>
      </el-table-column>
      <el-table-column label="" width="250px">
        <template slot-scope="scope">
          <el-button
            size="small"
            @click="previewHandler(scope.row)"></el-button>
          <el-button
            size="small"
            @click="handlePresentable(scope.row)" v-show="!scope.row.presentableDetail">user policy</el-button>
        </template>
      </el-table-column>
    </table-view>
  </section>
</template>

<script>
  import ResourceList from './index'

  export default ResourceList
</script>

<style lang="less" scoped>
  .query-input {
    margin-bottom: 15px;
    width: 50%;
  }

.resource-list {
  width: 100%;
  min-height: 600px;
}
</style>



